import { createRouter, createWebHistory } from 'vue-router'

const Login = () => import('@/views/TheLogin.vue')
const Forget = () => import('@/views/ForgetPassword.vue')
const Dashboard = () => import('@/views/dashboard/TheDashboard.vue')
const Profile = () => import('@/views/dashboard/TheProfile.vue')
const Employees = () => import('@/views/dashboard/TheEmployees.vue')
const NewEmployee = () => import('@/views/dashboard/NewEmployee.vue')
const ProfileEmployee = () => import('@/views/dashboard/EmployeeProfile.vue')

const routes = [
  {
    path: '/login',
    component: Login,
    alias: '/'
  },
  {
    path: '/forget',
    component: Forget
  },
  {
    path: '/dashboard',
    component: Dashboard,
    children: [
      {
        path: 'profile',
        component: Profile
        // props: true
      },
      {
        path: 'employees',
        component: Employees
      },
      {
        path: 'employee-create',
        component: NewEmployee
      },
      {
        path: 'employee-profile/:id',
        name: 'employee-profile',
        component: ProfileEmployee,
        props: true
      }
    ]
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  linkActiveClass: 'navigation__item-active',
  linkExactActiveClass: 'navigation__item-active',
  routes
})

export default router
